//Контейнер галереї

.gallery {
    margin-left: auto;
    margin-right: auto;

    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    
    @media screen and (min-width: 768px) {
        padding-top: 60px;
        padding-left: 32px;
        padding-right: 32px;
    }
}

.gallery__set {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;

    margin-top: -20px;

    @media screen and (min-width: 768px) {
        margin-left: -32px;
        margin-top: -32px;
    }

    @media screen and (min-width: 1280px) {
        margin-left: -16px;
        margin-top: -32px;
    }
}

.gallery__set__item {
    margin-top: 20px;

    @media screen and (min-width: 768px) {
        margin-left: 32px;
        margin-top: 32px;

        flex-basis: calc(100% / 2 - 32px);
    }

    @media screen and (min-width: 1280px) {
        margin-left: 16px;
        margin-top: 32px;

        flex-basis: calc(100% / 3 - 16px);
    }    
}